.modal {
  width: 400px;
  height: 150px;
  background-color: whitesmoke;
  position: absolute;
  top: 200px;
  left: calc(50% - 200px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}




.modal > div {
  display: flex;
  flex-direction: column;
}